// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import 'lib-flexible/flexible.js'
import vuePicturePreview from 'vue-picture-preview'
import {Popup, Swipe, SwipeItem } from 'mint-ui';
import $modal from './base/modal/index';
// import VueAwesomeSwiper from 'vue-awesome-swiper'
import '@/assets/css/normal.css'
import 'mint-ui/lib/style.css'
import '@/vender/jquery.min.js';
import '@/vender/jscex.min.js';
import '@/vender/jscex-parser.js';
// import '@/vender/jscex-jit.js';
import '@/vender/jscex-builderbase.min.js';
// import '@/vender/jscex-async.min.js';
// import '@/vender/jscex-async-powerpack.min.js';
import '@/vender/functions.js';
import '@/vender/love.js';
import '@/vender/love.js'
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
Vue.component(Popup.name, Popup);
Vue.prototype.$modal = $modal;
Vue.use(vuePicturePreview)
Vue.config.productionTip = false;
// Vue.use(VueAwesomeSwiper);
/* eslint-disable no-new */
router.beforeEach((to, from, next) => {
  // to and from are both route objects
  document.title = to.meta;
  next()
})
this.vm = new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



